export function CyfaResolver() {
  function pascalToKebab(pascalString: string) {
    return pascalString.replace(/[A-Z]/g, function (match: any, index: number) {
      return (index !== 0 ? '-' : '') + match.toLowerCase()
    })
  }

  return (componentName: string) => {
    if (componentName.startsWith('Cy')) {
      const component = `@cyware/cy-frontend-framework-v3/components/${pascalToKebab(componentName.slice(2))}/index.js`
      const sideEffects = `@cyware/cy-frontend-framework-v3/components/${pascalToKebab(componentName.slice(2))}/style/css.js`
      if (component) {
        return {
          name: componentName,
          from: component,
          sideEffects: sideEffects
        }
      }
    }
  }
}
